import React, {Component} from "react";
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalHeader, ModalBody, FormGroup, Label } from "reactstrap";
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent'
import {baseURL} from '../shared/baseURL'
import { FadeTransform, Fade, Stagger} from 'react-animation-components'

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len); 

function RenderDish({dish}) {

    if (dish != null) {
        return (
            <div className='col-12 col-md-5 m-1'>
                <FadeTransform  in transformProps={{ exitTransform: 'scale(0.5) traslateY(-50%)'}}>
                    <Card>
                        <CardImg width="100%" src={baseURL + dish.image} alt={dish.name} />
                        <CardBody>
                            <CardTitle> {dish.name}</CardTitle>
                            <CardText> {dish.description} </CardText>
                        </CardBody>
                    </Card>
                </FadeTransform>
            </div>   
        );
    }
    else {
        return (
            <div></div>
        );
    }
}

function RenderComments({comments, postComment, dishId}){
    if (comments == null) {
        return (<div></div>)
    }
    
    const cmnts = comments.map(comment => {
            return (
                <Stagger in>
                    <Fade in>
                        <li key={comment.id}>
                            <p>{comment.comment}</p>
                            <p>-- {comment.author},
                            &nbsp;
                            {new Intl.DateTimeFormat('en-US', {
                                year: 'numeric',
                                month: 'short',
                                day: '2-digit'
                            }).format(new Date(comment.date))}
                            </p>
                        </li>
                    </Fade>
                </Stagger>
            )

    })
    return (
        <div>
            <h4> Comments </h4>
            <ul className='list-unstyled'>
                {cmnts}
            </ul>
            <CommentForm dishId={dishId} postComment={postComment} />
        </div>
    )
}


const DishDetail = (props) => {
    const dish = props.dish
    if(props.isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if(props.errMess){
        <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
    }
    else if (dish == null) {
        return (<div></div>);
    }
    else{
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                <div className='row'>
                    <RenderDish dish={props.dish} />
                    <div className='col-12 col-md-5 m-1'>
                        <RenderComments comments={props.comments} 
                            postComment={props.postComment}
                            dishId={props.dish.id} />
                    </div>
                </div>
            </div>
        )
    }
}

class CommentForm extends Component{

    constructor(props){
        super(props);
        this.state = {
            isModalOpen: false
        };
        this.toggleModal = this.toggleModal.bind(this);
        this.handleComment = this.handleComment.bind(this);
    }

    toggleModal(){
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleComment(values){
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
        this.toggleModal();
    }

    render(){
        return(
            <div>
                <Button outline  onClick={this.toggleModal}>
                    <i class="fas fa-pencil-alt"></i> Submit Comment
                </Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={this.handleComment}>
                            <FormGroup>
                                <Label htmlFor="rating">Rating</Label>
                                <Control.select model=".rating" className="form-control" name="rating" id="rating" validators={{required}}>
                                    <option></option>
                                    <option>5</option>
                                    <option>4</option>
                                    <option>3</option>
                                    <option>2</option>
                                    <option>1</option>
                                </Control.select>
                                <Errors className="text-danger" model=".rating" show="touched" messages={{ required: 'Required'}} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="author">Name</Label>
                                <Control.text model=".author" className="form-control" validators={{required, minLength: minLength(2), maxLength: maxLength(15)}} id="author" name="author"  placeholder="Name" />
                                <Errors className="text-danger" model=".author" show="touched" messages={{ required: 'Required', minLength: '  Must be greater than 2 charecters', maxLength: '  Must be lesser than 15 charecters'}} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="message">Comment</Label>
                                <Control.textarea model=".comment" className="form-control" validators={{required}} id="comment" name="comment" rows="4"/>
                                <Errors className="text-danger" model=".comment" show="touched" messages={{ required: 'Required'}} />
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Submit</Button>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}


export default DishDetail;